import React from 'react';

import CardList from '../CardList';

function MovieView({ movieData, totalResults, lickPagination, page, setRatingMovie }) {
  return (
    <CardList
      movieData={movieData}
      totalResults={totalResults}
      lickPagination={(e) => lickPagination(e)}
      page={page}
      setRatingMovie={(item) => setRatingMovie(item)}
    />
  );
}

export default MovieView;
